<template>
    <div class="modal-wrapper">
        <div class="content order-modal">
            <span class="order-modal__close-btn">
                <i class="bi bi-x" @click="$emit('close')"></i>
            </span>
            <!-- <img :src="imgSrc" alt="" class="order-modal__img"> -->
            <MenuItemImg
             :imgId="item.imgId"
             :classes="['order-modal__img']"
            />
            <div class="main-content">
                <p class="h4 font-libre">{{item.title}}</p>
                <p class="order-modal__icons d-flex flex-wrap">
                    <span v-if="item.special" class="menu-item__special-badge">
                        <i class="bi bi-star-fill"></i>
                        Special
                    </span>
                    <span
                    data-toggle="tooltip"
                    data-placement="top"
                    v-for="(icon, index) in item.tags"
                    :key="index"
                    :title="svgIcons[icon].text"
                    v-html="svgIcons[icon].icon"
                    >
                    </span>
                </p>
                <p class="order-modal__info">{{item.description}}</p>
                <div class="order-modal__input-group" v-if="hasSizes">
                    <label class="order-modal__info">Select an option</label>
                    <div v-if="sizeErr" class="alert alert-danger d-flex" role="alert">
                        <i class="bi bi-info-circle"></i>
                        <span class="ml-1">Please select one option.</span>
                    </div>
                    <div
                     class="order-modal__input-group__input cursor-pointer d-flex align-items-center justify-content-between"
                     v-for="(size, index) in item.sizes"
                     :key="index"
                     @click="(sizeModel = size) && (activeSizeIdx = index)"
                    >
                        <span class="d-flex align-items-center">
                            <span
                             class="order-modal__input-group__input-indicator"
                             :class="{'active': activeSizeIdx === index}"
                            ></span>
                            <span class="mx-1">{{size.size}}</span>
                        </span>
                        <span>
                            +£{{size.price.toFixed(2)}}
                        </span>
                    </div>
                </div><!-- /Sizes -->
                <div class="order-modal__input-group" v-if="hasOptions">
                    <label class="order-modal__info">Add to order</label>
                    <div
                     class="order-modal__input-group__input cursor-pointer d-flex align-items-center justify-content-between"
                     v-for="(option, index) in item.options"
                     :key="index"
                    >
                        <span class="d-flex align-items-center">
                            <input :value="index" v-model="selectedOptions" type="checkbox">
                            <span class="mx-1">{{option.title}}</span>
                        </span>
                        <span>
                            +£{{option.price.toFixed(2)}}
                        </span>
                    </div>
                </div><!-- /Options -->
                <div class="order-modal__input-group">
                    <label class="order-modal__info">Special Requests</label>
                    <textarea v-model="specialRequests" class="order-modal__input-group__input" placeholder="Add them here. We'll do our best to make it happen"></textarea>
                </div>
            </div>
            <div class="footer d-flex justify-content-end align-items-enter">
                <div class="order-modal__input-group__input d-flex align-items-center ">
                    <span @click="qty > 1 && qty--">-</span>
                    <input type="text" v-model="qty">
                    <span @click="qty++">+</span>
                </div>
                <button @click="addToCart" class="button flex-grow-1">Add to my order £{{totalPrice.toFixed(2)}}</button>
            </div>
        </div>
    </div>
</template>

<script>
import MenuItemImg from '@/components/MenuItemImg'

export default {
    props: ['item', 'itemId', 'menuId', 'svgIcons'],
    components: {
        MenuItemImg
    },
    data(){
        return {
            qty: 1,
            specialRequests: '',
            hasOptions: this.item.options && this.item.options.length > 0,
            hasSizes: this.item.sizes && this.item.sizes.length > 0,
            selectedOptions: [],
            sizeModel: null,
            activeSizeIdx: -1,
            sizeErr: false,
            optionModels: []
        }
    },
    computed: {
        totalPrice() {
            if(!parseInt(this.qty) || this.qty < 1) this.qty = 1
            this.qty = parseInt(this.qty)
            let res = this.item.price
            if(this.sizeModel){
                res += this.sizeModel.price
            }
            if(this.optionModels.length > 0){
                this.optionModels.forEach(option => {
                    res += option.price
                })
            }
            return this.qty * res
        }
    },
    watch: {
        sizeModel(){
            this.sizeErr = false
        },
        selectedOptions(vals){
            this.optionModels = vals.map(v => this.item.options[v])
        }
    },
    methods: {
        addToCart(){
            if(this.hasSizes && !this.sizeModel){
                this.sizeErr = true
                return
            }

            let product = {
                title: this.item.title,
                price: this.item.price,
                qty: this.qty,
                totalPrice: this.totalPrice,
                specialRequests: this.specialRequests,
                menuId: this.menuId,
                itemId: this.itemId
            }
            if(this.hasSizes){
                product.size = this.sizeModel
            }
            if(this.hasOptions && this.optionModels.length > 0){
                product.options = this.optionModels
            }
            this.$store.dispatch('addProductToCart', product)
            this.$emit('close')
        }
    }
}
</script>

<style>
.order-modal .order-modal__img{
    height: 55vw;
    min-height: 200px;
    max-height: 350px;
}

.order-modal{
    position: relative;
}
.order-modal__close-btn{
    position: absolute;
    top: .5em;
    right: .5em;
    cursor: pointer;
    background-color: #fff;
    height: 35px;
    width: 33px;
    border-radius: 50%;
    font-size: 2em;
    line-height: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 2px 5px rgba(0,0,0,.2);
}

.order-modal__icons{
    gap: .25em;
    margin: .75em 0;
}

.order-modal__info{
    font-size: .9em;
    color: rgb(128, 128, 128);
}
label.order-modal__info{
    font-size: 1em;
    display: block;
    width: 100%;
}

.order-modal__input-group{
    margin-top: 2em;
}
.order-modal__input-group__input{
    display: block;
    width: 100%;
    margin-top: 1em;
    padding: .5em;
    border-radius: 2px;
    border: 1px solid rgb(133, 133, 133);
    min-height: 40px;
}
textarea.order-modal__input-group__input{
    min-height: 60px;
}

.order-modal .footer{
    padding-left: 1.5em !important;
    padding-right: 1.5em !important;
    gap: 1em;
}
.order-modal .footer .order-modal__input-group__input{
    margin-top: 0;
    flex-grow: 0;
    width: auto;
    height: 40px;
    padding: 0 1em;
}
.order-modal .footer .button{
    height: 40px;
    padding: 0 .5em;
    border-radius: 2px;
}

.order-modal .footer .order-modal__input-group__input span{
    font-size: 2em;
    cursor: pointer;
    user-select: none;
    color: rgb(148, 148, 148);
}
.order-modal .footer .order-modal__input-group__input span:hover{
    color: rgb(59, 59, 59);
}
.order-modal .footer .order-modal__input-group__input input{
    width: 40px;
    text-align: center;
    padding: 0 !important;
    border: none !important;
    font-size: 1.5em;
}

.order-modal__input-group__input-indicator{
    margin: 0 .5em 0 .3em;
    height: 10px;
    width: 10px;
    background-color: #fff;
    display: inline-block;
    border-radius: 50%;
    cursor: pointer;
    box-shadow: 0 0 0 3px #fff, 0 0 0 4px var(--primary-color);
}
.order-modal__input-group__input-indicator.active{
    background-color: var(--primary-color);
}
</style>