<template>
    <div class="menu-container" :id="menu.title.toLowerCase() + '-section'">
        <img class="menu-banner" :src="menuImgs[menu.imgId]" alt="">
        <p class="font-libre menu-title">{{menu.title}}</p>
        <p class="menu-subtitle" v-if="menu.description">{{menu.description}}</p>
        <div class="menu-items">
            <MenuItem
             v-for="(item, index) in menu.items"
             :key="index"
             :item="item"
             :itemId="index"
             :menuId="menuId"
            />
        </div>
    </div>
</template>

<script>
import MenuItem from '@/components/MenuItem'

import menuImg1 from '@/assets/menu/1.jpg'
import menuImg2 from '@/assets/menu/2.webp'
import menuImg3 from '@/assets/menu/3.webp'
import menuImg4 from '@/assets/menu/4.jpeg'
import menuImg5 from '@/assets/menu/5.jpg'


export default {
    props: ['menu', 'menuId'],
    components: {
        MenuItem
    },
    data: () => ({
        menuImgs: [menuImg1, menuImg2, menuImg3, menuImg4, menuImg5]
    })
}
</script>

<style>
.menu-container{
    margin-bottom: 3em;
}
.menu-container img{
    display: block;
    width: 100%;
    object-fit: cover;
    height: 200px;
}
.menu-title{
    margin: 1em 0;
    font-size: 1.2em;
    text-transform: capitalize;
}
.menu-subtitle{
    margin-top: -1em;
    font-size: .9em;
    color: rgb(139, 139, 139);
}

.menu-items{
    margin-top: 1em;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1.5em;
}

@media(max-width: 768px){
    .menu-items{
        grid-template-columns: 1fr;
    }
}
</style>