<template>
    <div class="modal-wrapper">
        <div class="content">
            <div class="main-content">
                <div class="title d-flex justify-content-between">
                    <p class="h6 font-libre">My Orders ({{cart.length}})</p>
                    <i @click="$emit('close')" class="bi bi-x-lg ml-1 cursor-pointer"></i>
                </div>
                <div class="cart-items"
                 v-for="(item, index) in cart"
                 :key="index"
                >
                    <div class="cart-item d-flex">
                        <div class="input d-flex align-items-center justify-content-center">
                            <span @click="(item.qty > 1) && (item.qty--)">-</span>
                            <input type="text" v-model="item.qty">
                            <span @click="item.qty++">+</span>
                        </div>
                        <div class="item-info mx-3 flex-grow-1">
                            <p>{{item.title}}</p>
                            <p class="text-small text-capitalize" v-if="item.size">{{item.size.size}} (£{{item.size.price.toFixed(2)}})</p>
                            <p class="text-small text-capitalize" v-if="item.options">
                                {{ item.options.map(option => `${option.title} (£${option.price.toFixed(2)})`).join(', ') }}
                            </p>
                            <div class="btns">
                                <CartEditBtn
                                 :id="index"
                                 :menuId="item.menuId"
                                 :itemId="item.itemId"
                                />
                                <span @click="deleteItem(index)">Remove</span>
                            </div>
                        </div>
                        <div class="item-price">
                            £{{item.totalPrice.toFixed(2)}}
                        </div>
                    </div>
                </div>
                <div class="cart-note">
                    Add Order Notes
                </div>
                <div class="cart-note d-flex aign-items-center justify-content-between">
                    <span>Subtotal</span>
                    <span>£{{subtotal.toFixed(2)}}</span>
                </div>
            </div>
            <div class="footer text-small">
                <div class="alert alert-danger d-flex" role="alert">
                    <i class="bi bi-info-circle"></i>
                    <span class="ml-1">We're not accepting online orders yet. Please contact us to complete the order.</span>
                </div>
                <button class="button" @click="handleClick">Continue to Checkout</button>
            </div>
        </div>
    </div>
</template>

<script>
import CartEditBtn from '@/components/CartEditBtn.vue'

export default {
    components: {
        CartEditBtn
    },
    data(){
        return {
            cart: [],
            subtotal: 0
        }
    },
    created(){
        this.cart = JSON.parse(JSON.stringify(this.$store.state.cart))
    },
    watch: {
        cart: {
            handler(){
                let subtotal = 0
                this.cart.forEach(item => {
                    if(!parseInt(item.qty) || item.qty < 1) item.qty = 1
                    item.qty = parseInt(item.qty)

                    let res = item.price
                    if(item.size){
                        res += item.size.price
                    }
                    if(item.options && item.options.length > 0){
                        item.options.forEach(option => {
                            res += option.price
                        })
                    }
                    item.totalPrice = item.qty * res
                    subtotal += item.totalPrice
                })
                this.subtotal = subtotal
                this.$store.commit('setCart', this.cart)
            },
            deep: true
        }
    },
    methods: {
        deleteItem(index){
            this.$store.commit('removeProductFromCart', index)
        },
        handleClick(){
            console.log(this.$store.state.cart)
        }
    }
}
</script>

<style>
.cart-item{
    --color: rgb(47, 46, 46);
    color: var(--color);
    margin-top: 2em;
}
.cart-item .input{
    max-height: 30px;
    width: 60px;
    margin-top: 0;
    padding: .1em .5em;
    border: 1px solid var(--color);
}
.cart-item .input input{
    width: 30px;
    text-align: center;
    border: none;
    color: var(--color);
}
.cart-item .input span{
    cursor: pointer;
    user-select: none;
}
.cart-item .item-info p{
    line-height: 1;
}
.cart-item .item-info p.text-small{
    margin-top: -.75em;
    margin-bottom: 0;
}
.cart-item .item-info .btns{
    color: #000;
    margin-top: .5em;
    font-size: .85em;
    color: var(--primary-color);
    cursor: pointer;
}
.cart-item .item-info .btns > span:first-child{
    margin-right: 1em;
}

.cart-note{
    border-top: 1px solid rgb(145, 145, 145);
    padding-top: 1em;
    margin-top: 1em;
}
</style>


<style scoped>
.modal-wrapper{
    justify-content: end;
}
.modal-wrapper .content{
    margin: 0;
    max-width: 100%;
    height: 100%;
    width: 320px;
    max-height: 100%;
    display: flex;
    flex-direction: column;
}
.modal-wrapper .footer{
    margin-top: auto;
    border-top: none;
}
.modal-wrapper .footer .button{
    flex-grow: 1;
}
.text-small{
    font-size: .8em;
}
.modal-wrapper .footer .button{
    display: block;
    width: 100%;
    padding: .5em;
    border-radius: 3px;
    font-size: 1.2em;
    background-color: #605e5e;
}

@media(max-width: 576px){
    .modal-wrapper .content{
        width: 100%;
    }
}
</style>