<template>
    <span>
        <span @click="showEditModal = true">Edit</span>
        <OrderEditModal
         v-if="showEditModal"
         @close="showEditModal = false"
         :id="id"
         :menuId="menuId"
         :itemId="itemId"
        />
    </span>
</template>

<script>
import OrderEditModal from '@/components/OrderEditModal'

export default {
    props: ['id', 'itemId', 'menuId'],
    components: {
        OrderEditModal
    },
    data: () => ({
        showEditModal: false
    })
}
</script>