<template>
    <div class="navbar justify-content-start">
        <span class="select d-none d-md-block">
            <select>
                <option value="Menu" selected>Menu</option>
            </select>
        </span>
        <nav class="nav flex-grow-1 d-flex flex-wrap">
            <a
             v-for="(link, index) in links"
             :key="index"
             :href="`#${link}-section`"
             :class="{'active': index === activeIdx}"
             @click="handleNavLinkClick(index)"
            >{{link}}</a>
        </nav>
        <CartBtn />
    </div>
</template>

<script>
import CartBtn from '@/components/CartBtn.vue'

export default {
    components: {
        CartBtn
    },
    data(){
        return {
            links: ['mezze', 'mains', 'salads', 'dessert', 'drinks'],
            activeIdx: 0
        }
    },
    methods: {
        handleNavLinkClick(index) {
            this.activeIdx = index
        }
    }
}
</script>

<style>
.navbar{
    border-bottom: 1px solid rgb(170, 170, 170);
    --color: rgb(146, 146, 146);
    color: var(--color);
    font-weight: .9em !important;
    height: 55px;
    position: sticky;
    position: -webkit-sticky;
    top: 0;
    background-color: #fff;
}
.navbar .select{
    padding: 0 1em;
    border-right: 1px solid var(--color);
    margin-right: 1em;
}
.navbar .select select{
    min-width: 180px;
    border: none;
    outline: none;
    background-color: transparent;
    color: var(--color);
}
.navbar .nav a{
    color: var(--color);
    padding: 0 .5em;
    text-transform: capitalize;
    text-decoration: none;
    position: relative;
    display: inline-block;

}
.navbar .nav a.active::after{
    content: '';
    display: block;
    width: 100%;
    height: 2px;
    background-color: var(--primary-color);
    position: absolute;
    left: 0;
    bottom: -17px;
}
.navbar .button{
    padding: .65em 1em;   
}

@media(max-width: 576px){
    .cart-btn-container{
        background-color: #fff;
        padding: 10px;
        position: fixed;
        bottom: 0;
        left: 0;
        width: 100%;
        border-top: 1px solid rgb(209, 209, 209);
        box-shadow: 0 -2px 3px 0 rgba(0, 0, 0, 0.1);
        padding: 1.5em;
        z-index: 9999;
    }
    .cart-btn-container .button{
        width: 100%;
        padding: .75em .5em;
    }
}
</style>