<template>
    <span>
        <span  class="d-none d-sm-block" v-if="cartLen == 0">
            <i class="bi bi-bag mx-1"></i>
            <span>0</span>
        </span>
        <span v-else class="cart-btn-container">
            <button @click="showCartModel = true" class="button">
                View Cart
                <i class="bi bi-bag mx-1"></i>
                <span>{{cartLen}}</span>
            </button>
        </span>
        <CartModal
         v-if="showCartModel"
         @close="showCartModel = false"
        />
    </span>
</template>

<script>
import CartModal from '@/components/CartModal.vue'

export default {
    components: {
        CartModal
    },
    data(){
        return {
            cartLen: 0,
            showCartModel: false
        }
    },
    created(){
        this.cartLen = this.$store.state.cart.length
    },
    watch: {
        '$store.state.cart': function() {
            this.cartLen = this.$store.state.cart.length
        }
    }
}
</script>