                <p class="menu-item__info">{{item.description}}</p>
                <div class="menu-item__meta">
                    <p class="menu-item__icons d-flex flex-wrap">
                        <span v-if="item.special" class="menu-item__special-badge">
                            <i class="bi bi-star-fill"></i>
                            Special
                        </span>
                        <span
                        data-toggle="tooltip"
                        data-placement="top"
                        v-for="(icon, index) in item.tags"
