<style>
.menu-item{
    border: 1px solid rgb(145, 145, 145);
    border-radius: 1px;
    overflow: hidden;
    height: 160px;
    display: flex;
}
.menu-item:hover{
    background-color: rgba(0, 0, 0, 0.05);
    cursor: pointer;
}
.menu-content{
    padding: 1em;
    width: 60%;
}
.menu-img{
    width: 40%;
}
.menu-img img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
}


.menu-item__title{
    font-size: .9em;
}
.menu-item__info{
    font-size: .8em;
    line-height: 1.1;
    color: rgb(114, 114, 114);
    margin: 1em 0
}

.menu-item__meta{
    margin-top: auto;
}
.menu-item__meta p{
    margin: 0;
}
.menu-item__meta .menu-item__price{
    font-size: .95em;
    color: rgb(114, 114, 114);
    margin-top: .25em;
}
.menu-item__meta .menu-item__icons{
    margin-top: -.5em;
    gap: .2em;
}
.menu-item__meta .menu-item__icons svg{
    height: 22px;
    width: 22px;
}
.menu-item__special-badge{
    background-color: var(--primary-color);
    color: #fff;
    height: 22px;
    line-height: 22px;
    padding: 0 .75em;
    border-radius: 20px;
    font-size: .8em;
}


@media(max-width: 1200px){
    .menu-content{
        padding: 1em;
        width: 50%;
    }
    .menu-img{
        width: 50%;
    }
}
@media(max-width: 992px){
    .menu-content{
        padding: 1em;
        width: 55%;
    }
    .menu-img{
        width: 45%;
    }
}
@media(max-width: 768px){
    .menu-content{
        padding: 1em;
        width: 70%;
    }
    .menu-img{
        width: 30%;
    }
}
@media(max-width: 576px){
    .menu-content{
        padding: 1em;
        width: 60%;
    }
    .menu-img{
        width: 40%;
    }
}
</style>